import React, {useEffect, useState} from 'react'
import { Link } from 'gatsby'
import { Nav, Navbar } from 'react-bootstrap'

const Header = () => {
    const [headFlag, setHeadFlag] = useState(0);
    const settingNavColor1 = () =>{ //home, contact
        let scrollTop  = document.documentElement.scrollTop //滚动条滚动高度
        let w = document.documentElement.offsetWidth || document.body.offsetWidth
        if(w>1400){
            if(scrollTop<1080){
                setHeadFlag(0)
            }
            if(scrollTop>1080 && scrollTop<2160){
                setHeadFlag(1)
            }
            if(scrollTop>2160 && scrollTop<3240){
                setHeadFlag(0)
            }
            if(scrollTop>3240 && scrollTop<4320){
                setHeadFlag(1)
            }
            if(scrollTop>4320 && scrollTop<5400){
                setHeadFlag(0)
            }
            if(scrollTop>5400 && scrollTop<6480){
                setHeadFlag(1)
            }
            if(scrollTop>6480){
                setHeadFlag(0)
            }

        }else{
            if(scrollTop<768){
                setHeadFlag(0)
            }
            if(scrollTop>768 && scrollTop<1476){
                setHeadFlag(1)
            }
            if(scrollTop>1476 && scrollTop<2148){
                setHeadFlag(0)
            }
            if(scrollTop>2148 && scrollTop<2916){
                setHeadFlag(1)
            }
            if(scrollTop>2916 && scrollTop<3684){
                setHeadFlag(0)
            }
            if(scrollTop>3684 && scrollTop<4392){
                setHeadFlag(1)
            }
            if(scrollTop>4392){
                setHeadFlag(0)
            }
    
        }
    }
    const settingNavColor2 = () =>{ //product, project, about
        let scrollTop  = document.documentElement.scrollTop //滚动条滚动高度
        let w = document.documentElement.offsetWidth || document.body.offsetWidth
        if(w>1400){
            if(scrollTop<1080){
                setHeadFlag(1)
            }
            if(scrollTop>1080 && scrollTop<2160){
                setHeadFlag(0)
            }
            if(scrollTop>2160 && scrollTop<3240){
                setHeadFlag(1)
            }
            if(scrollTop>3240 && scrollTop<4320){
                setHeadFlag(0)
            }
            if(scrollTop>4320 && scrollTop<5400){
                setHeadFlag(1)
            }
            if(scrollTop>5400 && scrollTop<6480){
                setHeadFlag(0)
            }
            if(scrollTop>6480){
                setHeadFlag(1)
            }

        }else{
            if(scrollTop<768){
                setHeadFlag(1)
            }
            if(scrollTop>768 && scrollTop<1476){
                setHeadFlag(0)
            }
            if(scrollTop>1476 && scrollTop<2148){
                setHeadFlag(1)
            }
            if(scrollTop>2148 && scrollTop<2916){
                setHeadFlag(0)
            }
            if(scrollTop>2916 && scrollTop<3684){
                setHeadFlag(1)
            }
            if(scrollTop>3684 && scrollTop<4392){
                setHeadFlag(0)
            }
            if(scrollTop>4392){
                setHeadFlag(1)
            }
    
        }
        
    }
    const handleScroll = (event) =>{
        //滚动条高度
        // let clientHeight = document.documentElement.clientHeight; //可视区域高度
        // var h = document.documentElement.offsetHeight || document.body.offsetHeight ;
        let pathname = window.location.pathname
        if(pathname =='/' || pathname =='/contact') {
            settingNavColor1()
        }else {
            settingNavColor2()
        }

    }
    useEffect(()=>{
        let pathname = window.location.pathname
        if(pathname =='/product' || pathname =='/project' || pathname =='/about') {
            setHeadFlag(1)
        }
        window.addEventListener('scroll', handleScroll)

      },[])
    
    return(
        // <header>
        //     <h1>
        //         <Link>
        //             Bocspace
        //         </Link>
        //     </h1>
        //     <nav>
                // <ul>
                //     <li><Link to="/">Home</Link></li>
                //     <li><Link to="/product">Product/Service</Link></li>
                //     <li><Link to="/">Proect Cases</Link></li>
                //     <li><Link to="/about">About Us</Link></li>
                //     <li><Link to="/news">News</Link></li>
                //     <li><Link to="/contact">Contact Us</Link></li>
                // </ul>
        //     </nav>
        // </header>
        <Navbar  className="header-nav" fixed="top" >
            <Link to="/">            
                <Navbar.Brand className="bocsapce-brand" >
                <img
                    alt=""
                    src="/home/logo@2x(2).png"
                    width="176"
                    height="24"
                    className="d-inline-block align-top"
                />
                </Navbar.Brand>
            </Link>
            <Nav className="mr-auto nav-group" style={{height:'25px', overflow:'hidden'}}>
                {/* <Nav.Link href="/">Home</Nav.Link>
                <Nav.Link href="/product">Product/Service</Nav.Link>
                <Nav.Link href="/project">Proect Cases</Nav.Link>
                <Nav.Link href="/about">About Us</Nav.Link>
                <Nav.Link href="/news">News</Nav.Link>
                <Nav.Link href="/contact">Contact Us</Nav.Link> */}
                <ul style={{overflow: 'hidden'}}>
                    <li><Link className={headFlag==0?'nav-item':'nav-item2'} to="/">Home</Link></li>
                    <li><Link className={headFlag==0?'nav-item':'nav-item2'} to="/product">Product/Service</Link></li>
                    <li><Link className={headFlag==0?'nav-item':'nav-item2'} to="/project">Project Cases</Link></li>
                    <li><Link className={headFlag==0?'nav-item':'nav-item2'} to="/about">About Us</Link></li>
                    {/* <li><Link className="nav-item" to="/news">News</Link></li> */}
                    <li><Link className={headFlag==0?'nav-item':'nav-item2'} to="/contact">Contact Us</Link></li>
                </ul>
            </Nav>
        </Navbar>
    )
}

export default Header